import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

export default new Router({
  routes:[
    {
      path:'',
      name:'main',
      component: () => import('@/views/Main.vue'),
    },
    {
      path:'/library',
      name:'library',
      component: () => import('@/views/library/index.vue'),
    },
  ],
  //默认是hash模式，带#
  //改为history模式，不带#
  mode:'history'
})



